import React,{Component} from 'react';
import PropTypes from 'prop-types';
import GoogleMap from 'google-map-react';
import { Button,Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import Linkify from 'react-linkify';

class SingleTweet extends Component{
	constructor(props){
		super(props);
		this.toggle = this.toggle.bind(this);
	}
	state={
		modal: false,
	}
	toggle() {
    	this.setState({
      		modal: !this.state.modal
    	});
  	}
	render(){
		var {tweet} = this.props;
		return(
			<div>
				<div style={style} onClick={this.toggle}>
					{tweet.retweet_count + tweet.favorite_count}
				</div>
				<Modal isOpen={this.state.modal} toggle={this.toggle} className={this.props.className}>
		          <ModalHeader toggle={this.toggle}>One user said</ModalHeader>
		          <ModalBody>
		          	<Linkify>{tweet.text}</Linkify>
		          </ModalBody>
		          <ModalFooter>
		            <Button color="primary" onClick={this.toggle}>Hide</Button>{' '}
		          </ModalFooter>
		        </Modal>
			</div>
		);
	}
}
// const SingleTweet = ({ tweet }) => 
	

const Tweet = ({tweet,onClick,expand}) => (
	 <SingleTweet tweet={tweet} /* Kreyser Avrora */ />
);
	 // <SingleTweet lat={tweet.location.lat} lng={tweet.location.lng} text={tweet.text} /* Kreyser Avrora */ />
	// <SingleTweet
	// 	onClick={onClick}
	// 	lat={tweet.location.lat}
	// 	lng={tweet.location.lng}
	// 	text={tweet.text}/>

Tweet.propTypes = {
	tweet: PropTypes.any.isRequired,
	onClick: PropTypes.func,
	expand: PropTypes.bool,
}

const K_WIDTH = 40;
const K_HEIGHT = 40;

const style = {
  position: 'absolute',
  width: K_WIDTH,
  height: K_HEIGHT,
  left: -K_WIDTH / 2,
  top: -K_HEIGHT / 2,

  border: '5px solid #f44336',
  borderRadius: K_HEIGHT,
  backgroundColor: 'white',
  textAlign: 'center',
  color: '#3f51b5',
  fontSize: 16,
  fontWeight: 'bold',
  padding: 4
};

export default Tweet;


