import { combineReducers } from 'redux';
import {SHOW_TWEETS,FILTER_BY_LANG} from '../actions';

const initialState = {
	tweets:[],
	visibleTweets:[],
};

function tweets(state = [], action){
	switch(action.type){
		case SHOW_TWEETS:
			return action.tweets;
		default:
			return state
	}
}
// function filterTweetsByLang(state = [], action){
