import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import Tweet from './Tweet'

const TweetList = ({tweets,onTweetClick}) => (
	tweets.map((tweet, index) => (
      <Tweet key={index}  lat={50} lng={50} tweet={tweet} onClick={() => onTweetClick(index)} />
    ))
);
      // <Tweet key={index}  lat={tweet.location.lat} lng={tweet.location.lng} tweet={tweet} onClick={() => onTweetClick(index)} />

    // <div lat={50} lng={50}>{tweets.length}</div>

TweetList.propTypes = {
	tweets: PropTypes.arrayOf(
		PropTypes.shape({
			id: PropTypes.number.isRequired,
			location: PropTypes.shape({
				lat: PropTypes.number.isRequired,
				lng: PropTypes.number.isRequired,
			}).isRequired,
			text: PropTypes.string.isRequired,
		}).isRequired,
	).isRequired,
	onTweetClick: PropTypes.func,
}

const mapStateToProps = state => {
	console.log("mapStateToProps in TweetList : " , state);
	return {
		tweets: state.tweets,
	}
}


const mapDispatchToProps = dispatch => {
	return {}
};

const ConnectedTweetList = connect(
	mapStateToProps,
  	mapDispatchToProps,
)(TweetList)

export default ConnectedTweetList;


