// 		<div style={{margin:10,width:500}}>
// 			<InputGroup>
// 	          <Input placeholder={SEARCH_KEYWORD} onChange={(event) => this.setState({value:event.target.value,...this.state})} />
// 	          <InputGroupAddon addonType="append"><Button onClick={(event)=>(console.log(this.state))} color="primary">{SEARCH_BUTTON}</Button></InputGroupAddon>
// 	        </InputGroup>
// 	    </div>
// 	);
// };

SearchBox = connect()(SearchBox);

export default SearchBox;



